function createColorGridItem(color) {
  const element = document.createElement('div');

  element.textContent = color;
  element.style.backgroundColor = color.replace(' ', '');

  return element;
}

function setupColorGrid() {
  const colors = [
    'snow',
    'ghost white',
    'white smoke',
    'gainsboro',
    'floral white',
    'old lace',
    'linen',
    'antique white',
    'blanched almond',
    'bisque',
    'peach puff',
    'navajo white',
    'moccasin',
    'cornsilk',
    'ivory',
    'lemon chiffon',
    'seashell',
    'honeydew',
    'mint cream',
    'azure',
    'alice blue',
    'lavender',
    'lavender blush',
    'misty rose',
  ];

  const colorGrid = document.getElementsByClassName('ColorGrid')[0];

  while (colorGrid.lastChild) {
    colorGrid.removeChild(colorGrid.lastChild);
  }

  colors
    .map(createColorGridItem)
    .forEach(element => colorGrid.appendChild(element));
}
